@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400&family=Poppins&display=swap");

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}
a:hover {
  color: white;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}
nav {
  z-index: 1;
}
body {
  min-height: 100vh;
}

.section-1 {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url("./assets/img1.jpg");
  background-size: cover;
  background-position: center;
  margin: 0 0 2rem 0;
  z-index: 10;
}
.div-overlay-1 {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  z-index: 1;
}

li {
  list-style: none;
}

.container {
  display: inline-block;
  cursor: pointer;
}

/* welcome */
.welcome {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: auto;
  color: rgb(218, 214, 214);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
  width: 100%;
  z-index: 10;
}

.welcome-message {
  font-size: max(2vw, 1.2rem);
  color: white;
  font-weight: 600;
  letter-spacing: 1px;
}
.type-message {
  font-size: max(4.5vw, 2rem);
  color: white;
  font-weight: 600;
  letter-spacing: 1px;
  text-align: center;
}
.type-messaage-div {
  text-align: center;
}
.address {
  font-size: max(1vw, 1rem);
}
.hire-btn button {
  background: none;
  outline: none;
  padding: 1vw 3vw;
  border: 2px solid #20c997;
  font-weight: 600;
  font-size: max(1.1vw, 0.6rem);
  border-radius: 3vw;
  color: #20c997;
  letter-spacing: 0.5px;
  margin-top: 1.5rem;
  transition: all 0.5s;
}
.hire-btn button:hover {
  color: white;
  background-color: #20c997;
}

@media (min-width: 1100px) {
  .collapse {
    display: none;
  }
}
.twitter:hover {
  color: rgb(0, 204, 255);
}
.facebook:hover {
  color: rgb(51, 49, 175);
}
.linkedin:hover {
  color: rgb(39, 171, 204);
}
.animated-icon {
  color: rgb(218, 214, 214);
  width: 100%;
  position: absolute;
  bottom: 3rem;
  font-size: max(1.8vw, 1rem);
  text-align: center;
  animation: scrollbtn 1.5s ease-out infinite;
  z-index: 100;
  font-size: 1.2rem;
}
@keyframes scrollbtn {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(2rem);
  }
}

.about {
  width: 100%;
  height: 100%;
  margin: 2rem 0;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: center;
}
.about-header-container,
.services-header-container,
.resume-header-container,
.portfolio-header-container,
.contact-header-container {
  position: relative;
  width: 100%;
  margin: 2rem 0 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7vw;
}
.teal {
  color: #20c997;
}
.about-text-container {
  padding: 3vw 5vw 3vw 10vw;
}
.about-details-container {
  padding: 2vw 3vw 2vw 3vw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.about-details-container div {
  padding: 0.7rem 1rem;
  border-bottom: 1px solid rgb(236, 231, 231);
}
@media (max-width: 960px) {
  .about-details-container {
    padding: 2vw 7vw 2vw 10vw;
  }
}
.header-1 {
  font-size: max(10vw, 4rem);
  text-align: center;
  font-weight: 700;
  color: #eeeff0;
  line-height: 0.5;
}
.header-2 {
  position: absolute;
  font-weight: 600;
  font-size: max(3vw, 2rem);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.border-bottom {
  width: 40%;
  height: 5px;
  border-radius: 2rem;
  background-color: #20c997;
}
.about-text-container-header > h1 {
  font-weight: 600;
  padding: 1vw 0;
  font-family: "Poppins", sans-serif;
}
@media (max-width: 770px) {
  .about-text-container {
    text-align: center;
  }
  .about-text-container-header > h1 {
    text-align: center;
  }
}
.download {
  margin-top: 1rem;
  max-width: 10rem;
  background-color: #20c997;
  border: none;
  border-radius: 2rem;
  padding: 1vw;
  color: white;
  box-shadow: 0 5px 0.5rem rgb(209, 208, 208);
  transition: all 0.5s;
  margin-left: 1rem;
}
.download:hover {
  background-color: teal;
  color: white;
  transform: translateY(-2px);
}
.services,
.portfolio {
  width: 100%;
  height: 100%;
  background-color: rgb(250, 250, 250);
  margin: 2rem 0;
  display: flex;
  justify-content: center;
}
.services-container {
  display: flex;
  justify-content: center;
  padding: 2rem;
}
.services-container h1 {
  font-size: max(2vw, 1.2rem);
  font-weight: 600;
  padding: 1vw 0;
  font-family: "Poppins", sans-serif;
}
.services-icon {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}
.icon {
  box-shadow: 1px 1px 5px 0 rgb(206, 200, 200);
  min-width: 4rem;
  min-height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  font-size: 1.5rem;
  color: #20c997;
}
.resume {
  width: 100%;
  height: 100%;
  margin: 2rem 0;
}
.resume-container-2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.resume-details {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.resume-details-container,
.resume-skills-container {
  border: 1px solid rgb(211, 211, 211);
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  justify-content: center;
  transition: all 0.5s;
  box-shadow: 1px 1px 10px 0 rgb(219, 218, 218);
}
.resume-details-container:hover {
  transform: translateY(-5px);
}
.resume-details-header {
  font-size: max(2vw, 1.3rem);
  font-weight: 600;
  padding: 1vw 0;
  font-family: "Poppins", sans-serif;
  text-align: center;
}
.resume-details-container-year {
  font-weight: 100;
  font-size: 0.8rem;
  color: white;
  background-color: #20c997;
  padding: 2px 4px;
  border-radius: 5px;
  width: max(15%, 6rem);
  text-align: center;
}
.resume-details-container-education {
  font-size: max(1.8vw, 1.1rem);
  font-weight: 600;
}
.resume-details-container-university {
  color: rgb(236, 115, 151);
}
.resume-details-container div {
  margin: 0.5rem 0;
}
.skills-details-container-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.inner-progress {
  height: 100%;
  background-color: #20c997;
  margin: 0;
  padding: 0;
  animation: progress 2s ease;
}
.progress {
  width: 100%;
  height: 8px;
  margin: 0;
  padding: 0;
  margin-top: 1rem;
}
.resume-skills-container {
  border: none;
  box-shadow: none;
}
@keyframes progress {
  0% {
    width: 0%;
  }
}
.title,
.percentage {
  font-weight: 600;
}
.resume-end-container {
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.download-2 {
  color: #333;
  background-color: white;
  border: 2px solid #20c997;
  border-radius: 2rem;
  padding: 1rem 2rem;
  transition: all 0.5s;
  font-weight: 600;
}
.download-2:hover {
  background-color: #20c997;
  color: white;
}
.portfolio-details-inner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.portfolio-details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.work {
  position: relative;
  margin: 1rem 2vw;
  padding: 0;
  border-radius: 0.7rem;
  box-shadow: 3px 3px 10px 0 grey;
  transition: all 0.5s;
}
.work img {
  border-radius: 0.7rem;
}
.div-overlay {
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: background-color 0.5s;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 1;
  border-radius: 0.7rem;
}
.div-overlay h1 {
  color: transparent;
  font-family: "poppins", sans-serif;
  font-size: max(2vw, 1.2rem);
  font-weight: 500;
  text-align: center;
}
.div-overlay span {
  color: transparent;
  font-family: "poppins", sans-serif;
  font-size: max(1.5vw, 1rem);
}
.work:hover {
  box-shadow: inset;
  transform: translateY(-5px);
}
.work:hover .div-overlay {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.7);
}
.work:hover .div-overlay h1 {
  color: white;
}
.work:hover .div-overlay span {
  color: white;
}
.work:hover .project-img {
  display: none;
}
.contact {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}
.contact-details {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 762px) {
  .contact-address-details,
  .contact-phone-details,
  .contact-social-details {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: 1rem 0;
  }
}
.contact-address-details p {
  margin: 0;
  font-family: "Poppins", sans-serif;
}
.contact-address-details h1,
.contact-social-details h1,
.contact-form h1 {
  font-family: "Poppins", sans-serif;
  font-size: max(2vw, 1.2rem);
  font-weight: 600;
  padding: 0.5rem 0;
  letter-spacing: 1px;
}
.icons i {
  color: grey;
  font-size: max(1.8vw, 1.3rem);
  padding: 0 1rem 0 0;
}
.phone i,
.email i {
  font-size: max(2vw, 1.2rem);
  color: #20c997;
}
.github:hover {
  color: #000000;
}
.hackerrank:hover {
  color: rgb(58, 161, 58);
}
.input-firstName input,
.input-email input,
.input-textarea textarea {
  width: 100%;
  padding: 1rem 1.5rem;
  margin-bottom: 0.5rem;
  border-radius: 3px;
  border: 1px solid rgb(221, 220, 220);
}
input:focus,
textarea:focus {
  outline: none;
}
.inputs {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.inputs div {
  width: 48%;
}
.send-msg-btn {
  max-width: 10rem;
  background-color: #20c997;
  border: none;
  border-radius: 2rem;
  padding: 1vw;
  color: white;
  box-shadow: 0 5px 0.5rem rgb(209, 208, 208);
  transition: all 0.5s;
  justify-self: center;
  align-self: center;
}
.send-msg-btn:hover {
  background-color: teal;
  color: white;
  transform: translateY(-2px);
}
.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact-form form {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.copyrights {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.active {
  color: #20c997;
}

.navbar-toggler i {
  color: #fff;
  outline: none;
}
